body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 16px; }

:focus {
  outline: none; }

.container {
  width: auto;
  margin: 0 auto;
  padding: 20px; }
  @media (min-width: 768px) {
    .container {
      width: 720px; } }
  @media (min-width: 1034px) {
    .container {
      width: 960px; } }

.float-rigth {
  float: right; }

.form-group {
  margin: 0 0 30px; }
  .form-group input {
    width: 100%;
    font-size: 18px;
    padding: 10px 0; }
  .form-group label {
    display: block;
    font-size: 14px; }

.input-effect {
  position: relative; }

.input-effect-border-animation {
  border: 0;
  padding: 7px 0;
  border-bottom: 1px solid #efe9f4; }
  .input-effect-border-animation ~ .focus-border {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    background-color: #2bc0ca;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s; }
  .input-effect-border-animation:focus ~ .focus-border {
    width: 100%;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s; }

.form-group {
  display: block; }



