.notifications {
  color: #fff;
  text-decoration: none;
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-size: 18px; }
  .notifications .message {
    position: relative;
    padding: 20px;
    background-color: #2bc0ca; }
  .notifications .msg-error {
    background-color: #e23b1c; }
  .notifications .close {
    position: absolute;
    right: 10px;
    top: 10px;
    color: #fff; }



