button,
a {
  border: 0;
  background-color: transparent;
  color: #2bc0ca;
  cursor: pointer;
  font-size: 16px;
  display: inline-block; }

.button-wrapper {
  position: relative;
  display: inline-block;
  background-color: #fff; }
  .button-wrapper.back-btn {
    margin-bottom: 20px; }
  .button-wrapper.danger button,
  .button-wrapper.danger a {
    color: #e23b1c; }
  .button-wrapper.danger .inner-wrapper:before {
    background-color: #e23b1c; }
  .button-wrapper.danger .inner-wrapper:after {
    background-color: #e23b1c; }
  .button-wrapper.danger .hover-effect-btn:before {
    background-color: #e23b1c; }
  .button-wrapper.danger .hover-effect-btn:after {
    background-color: #e23b1c; }

.inner-wrapper {
  position: relative;
  display: inline-block; }
  .inner-wrapper:before {
    content: '';
    position: absolute;
    background-color: #2bc0ca;
    bottom: 0;
    left: 0;
    width: 2px;
    height: 100%;
    -webkit-transform: scaleY(0);
        -ms-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: bottom left;
        -ms-transform-origin: bottom left;
            transform-origin: bottom left;
    -webkit-transition: -webkit-transform 0.2s;
    transition: -webkit-transform 0.2s;
    -o-transition: transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s; }
  .inner-wrapper:after {
    content: '';
    position: absolute;
    background-color: #2bc0ca;
    top: 0;
    right: 0;
    width: 2px;
    height: 100%;
    -webkit-transform: scaleY(0);
        -ms-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: top right;
        -ms-transform-origin: top right;
            transform-origin: top right;
    -webkit-transition: -webkit-transform 0.2s 0.2s;
    transition: -webkit-transform 0.2s 0.2s;
    -o-transition: transform 0.2s 0.2s;
    transition: transform 0.2s 0.2s;
    transition: transform 0.2s 0.2s, -webkit-transform 0.2s 0.2s; }
  .inner-wrapper .hover-effect-btn {
    padding: 10px; }
    .inner-wrapper .hover-effect-btn:before {
      content: '';
      position: absolute;
      background-color: #2bc0ca;
      top: 0;
      left: 0;
      width: 100%;
      height: 2px;
      -webkit-transform: scaleX(0);
          -ms-transform: scaleX(0);
              transform: scaleX(0);
      -webkit-transform-origin: top left;
          -ms-transform-origin: top left;
              transform-origin: top left;
      -webkit-transition: -webkit-transform 0.2s 0.3s;
      transition: -webkit-transform 0.2s 0.3s;
      -o-transition: transform 0.2s 0.3s;
      transition: transform 0.2s 0.3s;
      transition: transform 0.2s 0.3s, -webkit-transform 0.2s 0.3s; }
    .inner-wrapper .hover-effect-btn:after {
      content: '';
      position: absolute;
      background-color: #2bc0ca;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 2px;
      -webkit-transform: scaleX(0);
          -ms-transform: scaleX(0);
              transform: scaleX(0);
      -webkit-transform-origin: bottom right;
          -ms-transform-origin: bottom right;
              transform-origin: bottom right;
      -webkit-transition: -webkit-transform 0.2s 0.1s;
      transition: -webkit-transform 0.2s 0.1s;
      -o-transition: transform 0.2s 0.1s;
      transition: transform 0.2s 0.1s;
      transition: transform 0.2s 0.1s, -webkit-transform 0.2s 0.1s; }
  .inner-wrapper:hover:before {
    -webkit-transform: scaleY(1);
        -ms-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transition: -webkit-transform 0.2s 0.3s;
    transition: -webkit-transform 0.2s 0.3s;
    -o-transition: transform 0.2s 0.3s;
    transition: transform 0.2s 0.3s;
    transition: transform 0.2s 0.3s, -webkit-transform 0.2s 0.3s; }
  .inner-wrapper:hover:after {
    -webkit-transform: scaleY(1);
        -ms-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transition: -webkit-transform 0.2s 0.1s;
    transition: -webkit-transform 0.2s 0.1s;
    -o-transition: transform 0.2s 0.1s;
    transition: transform 0.2s 0.1s;
    transition: transform 0.2s 0.1s, -webkit-transform 0.2s 0.1s; }
  .inner-wrapper:hover .hover-effect-btn:before {
    -webkit-transform: scaleX(1);
        -ms-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transition: -webkit-transform 0.2s;
    transition: -webkit-transform 0.2s;
    -o-transition: transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s; }
  .inner-wrapper:hover .hover-effect-btn:after {
    -webkit-transform: scaleX(1);
        -ms-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transition: -webkit-transform 0.2s 0.2s;
    transition: -webkit-transform 0.2s 0.2s;
    -o-transition: transform 0.2s 0.2s;
    transition: transform 0.2s 0.2s;
    transition: transform 0.2s 0.2s, -webkit-transform 0.2s 0.2s; }



